import React from 'react';
export default class Summary extends React.Component {
    constructor(props) {
        super(props);
        this.initState().initBind();
    }

    initState() {
        this.state = {
            number: "",
            password: "",
            messages: [],
            comments: []
        }
        return this;
    }

    initBind() {
        this.handleNumberChanged = this.handleNumberChanged.bind(this);
    }

    componentDidMount() {
        this.retrieveLeaveMessage();
        this.retrieveNewComment();
    }

    retrieveLeaveMessage() {
        this.state.messages = [
            { message: '', author: '' },
            { message: '', author: '' },
            { message: '', author: '' },
            { message: '', author: '' },
            { message: '', author: '' },
        ]
        this.setState(this.state);
    }

    retrieveNewComment() {
        this.state.comments = [
            { message: '', author: '' },
            { message: '', author: '' },
            { message: '', author: '' },
            { message: '', author: '' },
            { message: '', author: '' },
        ]
        this.setState(this.state);
    }

    renderLeaveMessage() {
        return this.state.messages.map((m, index) => {
            return <li key={index}>
                <div>{m.message}</div>
                <div>----{m.author}</div>
                <div className='clear-both'></div>
            </li>
        })
    }

    renderNewComment() {
        return this.state.comments.map((m, index) => {
            return <li key={index}>
                <div>{m.message}</div>
                <div>----{m.author}</div>
                <div className='clear-both'></div>
            </li>
        })
    }

    handleNumberChanged(e) {
        this.setState(Object.assign(this.state, {
            number: e.target.value
        }))
    }

    handlePasswordChanged(e) {
        this.setState(Object.assign(this.state, {
            password: e.target.value
        }))
    }

    render() {
        return <div className='summary'>
            <section className='login'>
                <h3></h3>
                <div className='margin-bottom-10 margin-top-10'>
                    <label></label>
                    <input type='text' value={this.state.number} onChange={this.handleNumberChanged} />
                </div>
                <div className='margin-bottom-10'>
                    <label></label>
                    <input type='text' value={this.state.password} onChange={this.handlePasswordChanged} />
                </div>
                <RButton text='' />
                <RButton text='' style={{ marginLeft: '5px' }} />
            </section>
            <section>
                <h3></h3>
                <input className='margin-bottom-10 margin-top-10' type='text' placeholder="" />
                <RButton text='' style={{ marginLeft: '10px' }} />
            </section>
            <section className='about-me'>
                <h3></h3>
                <p></p>
                <p>26</p>
                <p></p>
                <p>Web</p>
                <p>PS</p>
                <div className='know-more'></div>
                <ul>
                    <li>
                        <a href="https://github.com/Dqhan" target="_blank">
                            <span title='GitHub' style={{ backgroundImage: `url(${require('../../Image/git.png')})` }}></span>
                        </a>
                    </li>
                    <li>
                        <a href="https://www.cnblogs.com/moran1992/" target="_blank">
                            <span title='' style={{ backgroundImage: `url(${require('../../Image/blog.png')})` }}></span>
                        </a>
                    </li>
                    <li>
                        <a href="https://www.dqhanhouse.com" target="_blank">
                            <span title='' style={{ backgroundImage: `url(${require('../../Image/share.png')})` }}></span>
                        </a>
                    </li>
                </ul>
            </section>
            <section>
                <h3></h3>
                <div className='tags'>
                    <span style={{ background: '#FF0000' }}>Html5</span>
                    <span style={{ background: '#33FF00' }}>JavaScript</span>
                    <span style={{ background: '#FF66FF' }}>Css3</span>
                    <span style={{ background: '#FF9900' }}>React</span>
                    <span style={{ background: '#00FFFF' }}>MongoDB</span>
                    <span style={{ background: '#CC66FF' }}>Node</span>
                    <span style={{ background: '#0000FF' }}></span>
                    <span style={{ background: '#666666' }}></span>
                </div>
            </section>
            <section className='new-comments'>
                <h3></h3>
                <ul>
                    {
                        this.renderNewComment()
                    }
                </ul>
            </section>
            <section className='new-leave-message'>
                <h3></h3>
                <ul>
                    {
                        this.renderLeaveMessage()
                    }
                </ul>
            </section>
        </div>
    }
}