import React from 'react';
import {
    Link
} from 'react-router-dom';
export default class Header extends React.Component {
    render() {
        return <header className={this.props.headerScrolled ? 'header header-scrolled' : 'header'}>
            <div className='container'>
                <h1>Dqhan's Blog</h1>
                <nav className='nav-bar'>
                    <ul>
                        <li className='nav-item'><Link to='/'></Link></li>
                        <li className='nav-item'><Link to='/sub/workmark'></Link></li>
                        <li className='nav-item'><Link to='/sub/blog'></Link></li>
                        <li className='nav-item'><Link to='/sub/production'></Link></li>
                        <li className='nav-item'><Link to='/sub/about'></Link></li>
                    </ul>
                </nav>
            </div>
        </header>
    }
}